<script>
export default {
  data() {
    return {
      enrollment: [],
      notification: []
    }
  },
  methods: {
    async getArticleList() {
      let Resources = await this.$api.getArticleList();
      this.enrollment = Resources.data.enrollment;
      this.notification = Resources.data.notification;
    },
    Wow() {
      let wow = new this.$Wow.WOW({
        boxClass: 'wow',
        animateClass: 'animate__animated',
        offset: 300,
        mobile: true,
        live: true
      });
      wow.init();
    }
  },
  created() {
    this.getArticleList()
  },
  mounted() {
    this.Wow()
  }
}
</script>

<template>
  <div class="article-list">
    <div class="content-box">
      <div class="card">
        <div class="title">
          <div class="title-content wow animate__animated animate__zoomIn">
            <i class="document-icon"></i>招生信息
          </div>
          <div class="title-button"><a href="#">MORE</a></div>
        </div>
        <div class="parting-line wow animate__animated animate__zoomIn"></div>
        <div class="card-content">
          <ul class="wow animate__animated animate__fadeInLeft">
            <li v-for="value in enrollment">
              <div></div>
              <div><a :href="value.link">{{ value.title }}</a></div>
              <div>{{ value.data }}</div>
            </li>
          </ul>
        </div>
      </div>
      <div class="card">
        <div class="title">
          <div class="title-content wow animate__animated animate__zoomIn">
            <i class="notification-icon"></i>通知公告
          </div>
          <div class="title-button"><a href="#">MORE</a></div>
        </div>
        <div class="parting-line wow animate__animated animate__zoomIn"></div>
        <div class="card-content">
          <ul class="wow animate__animated animate__fadeInRight">
            <li v-for="value in notification">
              <div></div>
              <div><a :href="value.link">{{ value.title }}</a></div>
              <div>{{ value.data }}</div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped lang="stylus">
.document-icon
  display block
  width 25px
  height 25px
  background url("https://static-cshu.cloudmiao.cn/static/sheke/images/Chinese/icon/document.png") no-repeat
  background-size 25px

.notification-icon
  display block
  width 25px
  height 25px
  background url("https://static-cshu.cloudmiao.cn/static/sheke/images/Chinese/department-news/icon/notification.png") no-repeat
  background-size 25px

.article-list
  margin 30px 0
  width 100%

  .content-box
    display flex
    justify-content space-between
    margin 0 auto
    width 1200px

.card
  width 575px

  .title
    display flex
    align-items center
    justify-content space-between

    .title-content
      display flex
      align-items center
      font-size 24px
      font-weight bold

      > i
        margin-right 5px

    .title-button
      > a
        font-size 14px
        font-weight bold
        color #725ba1

  .parting-line
    margin 20px 0
    width 100%
    height 1px
    background #725ba1

  .card-content
    width 100%

    > ul
      > li
        display flex
        align-items center
        justify-content space-between
        width 100%

        > div:first-of-type
          width 15px
          height 35px
          background url("https://static-cshu.cloudmiao.cn/static/sheke/images/Chinese/article-list/notice_list_bg.png") no-repeat center left

        > div:nth-of-type(2)
          > a
            display block
            width 465px
            overflow hidden
            white-space nowrap
            text-overflow ellipsis

        > div:last-of-type
          color #725ba1
</style>